
const Authentication = (props) => {

    return (
      <div className="verifyPage">
        <div className="brandingSection">
            <div className="logo"></div>
            <h2>Learned</h2>
            <p>Remember everything important.</p>
        </div>
        <button>Continue with Google</button>
        <input className="emailHandler" onChange={function(ev) {
            props.setEmail(ev.target.value)
        }}>
        </input>
        <input className="passwordHandler" onChange={function(ev) {
            props.setPassword(ev.target.value)
        }}>
        </input>
        <button onClick={function(ev) { props.submitLogin()}}>Continue</button>
        
      </div>
    )
  }
  
export default Authentication;